body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.pimg1 {
  position: relative;
  opacity: 0.78;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.ptext {
    position: absolute;
    top: 50%;
    width: 100%;
    text-align: center;
    color: #000;
    font-size: 27px;
    letter-spacing: 8px;
    text-transform: uppercase;

}

.ptext .border {
    background-color: #111;
    color: white;
    padding: 20px;
}

.subcontent{
    margin-top: 10em;
}

.deactivated {
    display: none
}
.section {
    text-align: center;
    padding: 50px 80px;
    font-size: 1em;
}
.section-light {
    background-color: #f4f4f4;
    color: #666;
}
.section-dark {
    background-color: #282e34;
    color: #ddd;
}
